@import "../../Assets/Fonts/fonts.scss";
@import "../../Assets/Colors/colors.scss";

.nav {
  height: 8.125rem;
  width: 100%;
  font-family: $font-roboto;

  &__bottom {
    height: 5.625rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.3125rem 1.25rem 0.3125rem 1.25rem;
    background-color: white;
    border-bottom: 1px solid black;

    &__left {
      display: flex;
      align-items: center;

      img {
        margin-right: 3.125rem;
        object-fit: contain;
        width: 8.125rem;
      }
    }
    &__right {
      display: flex;
      align-items: center;

      input {
        outline: none;
        border: none;
        border-bottom: 1px solid gray;
        width: 12.5rem;
        height: 1.25rem;
        margin-right: 1.25rem;
      }
      p {
        margin-left: 0.625rem;
        font-size: 1.125rem;
        text-align: center;
        font-family: $font-roboto;
        background-color: $pink-light;
        color: white;
        padding: 0.5625rem;
        width: 1.25rem;
        border-radius: 999px;
      }

      &__icon {
        height: 1.5625rem;
        width: 1.5625rem;
      }
    }
  }
}

@media screen and (min-width: 320px) and (max-width: 425px) {
  html {
    font-size: 10px;
  }
}

@media screen and (min-width: 425px) and (max-width: 623px) {
  html {
    font-size: 10px;
  }
}
@media screen and (min-width: 623px) and (max-width: 768px) {
  html {
    font-size: 10px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
  html {
    font-size: 10px;
  }
}
